/*modal*/
body :global {
  /*单选*/
  /*table*/
}
body :global .ant-modal-title {
  text-align: center;
  font-size: 28px;
}
body :global .ant-modal-header {
  padding: 34px 24px;
}
body :global .ant-modal-footer {
  display: flex;
  justify-content: center;
}
body :global .ant-modal-footer .ant-btn {
  width: 158px;
}
body :global .ant-modal-footer .ant-btn-primary {
  border-color: #0b7bff;
  background: #0b7bff;
}
body :global .ant-modal-footer .ant-btn:hover,
body :global .ant-modal-footer .ant-btn:focus {
  border-color: #0b7bff;
  color: #0b7bff;
}
body :global .ant-modal-footer .ant-btn-primary:hover,
body :global .ant-modal-footer .ant-btn-primary:focus {
  color: #fff;
}
body :global .ant-modal-confirm-btns .ant-btn-primary {
  border-color: #0b7bff;
  background: #0b7bff;
}
body :global .ant-cascader-menu {
  color: #4a4a4a;
}
body :global .ant-radio-button-wrapper {
  background: transparent !important;
  color: #fff;
  border-radius: 4px !important;
  border-left: 1px solid #d9d9d9;
}
body :global .ant-radio-button-wrapper-checked,
body :global .ant-radio-button-wrapper-checked:first-child {
  color: #26eafd;
  border-color: #26eafd;
}
body :global .ant-radio-button-wrapper:not(:first-child)::before {
  display: none;
}
body :global .ant-cascader-picker {
  background-color: transparent;
  color: #fff;
}
body :global .ant-cascader-picker-arrow {
  color: #fff;
}
body :global .ant-list-split .ant-list-item {
  border-bottom: none;
  padding: 0;
}
body :global .ant-list-something-after-last-item .ant-spin-container > .ant-list-item:last-child {
  border-bottom: none !important;
}
body :global .ant-list-something-after-last-item .ant-spin-container > .ant-list-items > .ant-list-item:last-child {
  border-bottom: none !important;
}
body :global .ant-empty-description {
  color: #fff;
}
body :global .ant-radio-button-wrapper-checked,
body :global .ant-radio-button-wrapper-checked:hover {
  box-shadow: none;
}
body :global .ant-table-placeholder {
  background: transparent;
}
.scrollbar {
  margin: 10px auto 0;
  overflow-y: auto;
}
::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 10px;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 10px;
}
::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 10px;
  background: -webkit-linear-gradient(#7ab3d2, #52bdf2);
  /* Safari 5.1 - 6.0 */
  /* Opera 11.1 - 12.0 */
  /* Firefox 3.6 - 15 */
  background: -webkit-gradient(linear, left top, left bottom, from(#7ab3d2), to(#52bdf2));
  background: linear-gradient(#7ab3d2, #52bdf2);
  /* 标准的语法 */
}
::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  border-radius: 10px;
  background: #efefef;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
