body :global {
  /*分页*/
  /*表格*/
}
body :global .ant-pagination-item-active {
  border-color: #26eafd;
}
body :global .ant-pagination-item,
body :global .ant-pagination-options-quick-jumper input {
  background: transparent;
  color: #fff;
}
body :global .ant-pagination-item a,
body :global .ant-pagination {
  color: #fff;
}
body :global .ant-pagination-item-active a {
  color: #26eafd;
}
body :global .ant-pagination-prev .ant-pagination-item-link,
body :global .ant-pagination-next .ant-pagination-item-link {
  background: transparent;
  color: #fff;
}
body :global .ant-table-thead > tr {
  background: transparent;
}
body :global .ant-table-thead > tr > th {
  color: #fff;
  background: transparent !important;
}
body :global .ant-table-bordered .ant-table-thead > tr > th,
body :global .ant-table-bordered .ant-table-tbody > tr > td {
  border-right: 1px solid #26eafd !important;
  border-bottom: 1px solid #26eafd !important;
}
body :global .ant-table-bordered .ant-table-body > table {
  border: 1px solid #26eafd;
}
body :global .ant-table-body {
  color: #fff;
  font-size: 16px;
}
body :global .ant-table-thead > tr.ant-table-row-hover:not(.ant-table-expanded-row) > td,
body :global .ant-table-tbody > tr.ant-table-row-hover:not(.ant-table-expanded-row) > td,
body :global .ant-table-thead > tr:hover:not(.ant-table-expanded-row) > td,
body :global .ant-table-tbody > tr:hover:not(.ant-table-expanded-row) > td {
  background: transparent;
}
